<template>
    <view
            class="cl-input"
            :class="[ { 'cl-input--prefix': prefixIcon, 'cl-input--suffix': suffixIcon, 'cl-input--focus': isFocus, 'is-disabled': disabled, 'is-fill': fill, 'is-round': round, 'is-border': border } ]"
    >
        <view class="cl-input__prepend" v-if="$slots.prepend">
            <slot name="prepend"></slot>
        </view>

        <view class="cl-input__wrap">
            <text
                    class="cl-input__icon"
                    :class="[prefixIcon]"
                    v-if="prefixIcon"
            ></text>
            <text
                    class="cl-input__icon"
                    :class="[suffixIcon]"
                    v-if="suffixIcon"
            ></text>

            <input
                    class="cl-input__inner"
                    v-model="value2"
                    type="text"
                    :password="password"
                    :placeholder="placeholder"
                    :disabled="disabled"
                    :focus="focus"
                    :placeholder-style="placeholderStyle"
                    :placeholder-class="placeholderClass"
                    :maxlength="maxlength"
                    :cursor-spacing="cursorSpacing"
                    :confirm-type="confirmType"
                    :confirm-hold="confirmHold"
                    :adjust-position="adjustPosition"
                    :holdKeyboard="holdKeyboard"
                    @input="onInput"
                    @focus="onFocus"
                    @blur="onBlur"
                    @confirm="onConfirm"
                    @keyboardheightchange="onKeyboardheightchange"
            />

            <text
                    class="cl-input__clear cl-icon-round-close-fill"
                    @tap="clear"
                    v-if="isFocus && clearable"
            ></text>
        </view>

        <view class="cl-input__append" v-if="$slots.append">
            <slot name="append"></slot>
        </view>
    </view>
</template>

<script>
    export default {
        name:'SunnyInput',
        props: {
            value: [String, Number],
            type: {
                type: String,
                default: "text"
            },
            password: Boolean,
            placeholder: String,
            clearable: Boolean,
            fill: Boolean,
            placeholderStyle: String,
            placeholderClass: String,
            disabled: Boolean,
            round: Boolean,
            border: {
                type: Boolean,
                default: true
            },
            focus: Boolean,
            maxlength: {
                type: [Number, String],
                default: 140
            },
            cursorSpacing: {
                type: Number,
                default: 0
            },
            confirmType: {
                type: String,
                default: "done"
            },
            confirmHold: Boolean,
            adjustPosition: {
                type: Boolean,
                default: true
            },
            holdKeyboard: {
                type: Boolean,
                default: false
            },
            prefixIcon: String,
            suffixIcon: String
        },

        data() {
            return {
                value2: null,
                isFocus: false
            };
        },

        watch: {
            value: {
                immediate: true,
                handler(val) {
                    this.value2 = val;
                }
            }
        },

        methods: {
            onInput(e) {
                this.$emit("input", e.detail.value);
                this.$emit("change", e.detail.value);
            },

            onFocus(e) {
                this.$emit("focus", e);
                this.isFocus = true;
            },

            onBlur(e) {
                this.$emit("blur", e);

                setTimeout(() => {
                    this.isFocus = false;
                }, 0);
            },

            onConfirm(e) {
                this.$emit("confirm", e);
                this.search();
            },

            onKeyboardheightchange(e) {
                this.$emit("keyboardheightchange", e);
            },

            search() {
                this.$emit("search", this.value2);
            },

            clear() {
                this.value2 = "";
                this.$emit("input", "");
                this.$emit("change", "");
            }
        }
    };
</script>

<style lang="scss">
    $color-error: #dd524d;
    .cl-input {
        display: flex;
        align-items: center;
        height: 70rpx;
        width: 100%;
        line-height: 68rpx;
        background-color: #fff;
        position: relative;
        border-radius: 8rpx;
        box-sizing: border-box;
        transition: border-color 0.2s;
        padding: 0 20rpx;

        &--focus {
            border-color: #3a8ee6;
        }

        &.is-fill {
            width: 100%;
        }

        &.is-disabled {
            background-color: #f5f7fa;
        }

        &.is-border {
            border: 1rpx solid #dcdfe6;
        }

        &__wrap {
            display: flex;
            flex: 1;
            height: calc(100% - 2rpx);
            position: relative;
        }

        &__inner {
            height: 100%;
            flex: 1;
            font-size: 26rpx;
            border: 0;
            padding: 0;
            outline: none;
            background-color: transparent;
        }

        &__prepend,
        &__append {
            font-size: 26rpx;
            line-height: normal;
        }

        &__prepend {
            margin-right: 20rpx;
        }

        &__append {
            margin-left: 20rpx;
        }

        &__clear,
        &__icon {
            display: inline-block;
            font-size: 34rpx;
            color: #999;
        }

        &__clear {
            color: #c1c1c1;
        }

        &:not(.is-disabled) {
            -webkit-box-shadow: 0 0 0 1000px transparent inset;
        }

        &--prefix,
        &--suffix {
            flex-shrink: 0;

            .cl-input__icon {
                position: absolute;
                top: 0;
                width: 35rpx;
                text-align: center;
            }
        }

        &--prefix {
            .cl-input__wrap {
                padding-left: 55rpx;
            }

            .cl-input__icon {
                left: 0;
            }
        }

        &--suffix {
            .cl-input__wrap {
                padding-right: 50rpx;
            }

            .cl-input__icon {
                right: 0;
            }
        }

        &.is-disabled {
            background-color: #f5f7fa;

            .cl-input__inner {
                background-color: #f5f7fa !important;
            }
        }

        &.is-round {
            border-radius: 100rpx;

            .cl-input__inner {
                border-radius: 100rpx;
            }
        }
    }

    .cl-form-item--error {
        input {
            border-color: $color-error;
        }
    }

    .cl-textarea {
        border: 1rpx solid #dcdfe6;
        border-radius: 8rpx;
        height: 150rpx;
        width: 100%;
        box-sizing: border-box;
        background-color: #fff;
        font-size: 26rpx;
        padding: 4rpx 10rpx;
        line-height: 40rpx;
    }


</style>
